import React from "react";

import "./card.styles.css";

const Card = (props) => (
  <div className="card">
    <p className="date">
      {new Date(props.data.date).toLocaleDateString("en-US", {
        weekday: "long",
      })}
    </p>
    <img src={props.data.day.condition.icon} alt="" />
    <p className="temp">{Math.floor(props.data.day.maxtemp_c)} Â°C</p>
    <p className="text">{props.data.day.condition.text}</p>
  </div>
);

export default Card;
