import React from "react";

import Header from "../components/header/header";
import Input from "../components/input/input";
import CardList from "../components/card-list/card-list";

import "./App.css";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      weatherData: [],
      city: "Plovdiv",
      userInput: "",
      lastSuccessfullSearch: "Plovdiv",
    };

    this.fetchWeather();
  }
  fetchWeather() {
    fetch(
      `https://api.weatherapi.com/v1/forecast.json?key=ecdee30c3b1e4c1cb0d83252201508&q=${this.state.city}&days=3`
    )
      .then((response) => response.json())
      .then((weatherData) => {
        this.setState({
          weatherData: weatherData.forecast.forecastday,
          lastSuccessfullSearch: weatherData.location.name,
        });
      })
      .catch((err) => console.log(err));
  }

  handleUserInput = (event) => {
    this.setState({ userInput: event.target.value });
  };

  handleKeyDown = (event) => {
    if (event.key === "Enter") {
      this.handleSearch();
    }
  };

  handleSearch = () => {
    const city = this.state.userInput;
    this.setState(
      {
        city,
      },
      () => {
        this.fetchWeather();
      }
    );
  };

  render() {
    return (
      <div className="App">
        <Header city={this.state.lastSuccessfullSearch} />
        <Input
          handleSearch={this.handleSearch}
          handleUserInput={this.handleUserInput}
          handleKeyDown={this.handleKeyDown}
        />
        <CardList weatherData={this.state.weatherData} />
      </div>
    );
  }
}

export default App;
